  .contact_list{
    margin: 0 auto;
    padding: 0;
  }

  .contact_list_item{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap;
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.06em;
    border-bottom: 1px solid #00000057;
  }

  .contact_list_values{
    margin-right: 15px;
    max-width: 350px;
    overflow-x: hidden;
  }

  .contact_list_btn_wrapper{
    display: flex;
    gap: 5px;
    margin-left: auto;
    margin-right: 5px;
  }

  .contact_list_btn{
    padding: 4px 5px;
    min-width: 25px;
    height: 25px;
  
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
  
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.1;
    letter-spacing: 0.06em;
    text-transform: capitalize;
    color: #ffffff;
  
    background-color: #2196f3;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    border-color: transparent;
    transition: scale, fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .contact_list_btn:hover,
  .contact_list_btn:focus{
    scale: 1.04;
  }

  .contact_list_btn:active{
    border-color: #2a2a2a;
  }

